import React from 'react'
import './App5.css'
import Hero from './Hero'
import Sidebar from './Sidebar'
import Navbar from './Navbar'
import Submenu from './Submenu'
import {AppProvider} from './Context'


function App5() {
    return (
        <>
        <AppProvider>
            <Navbar/>
            <Sidebar/>
            <Hero/>
            <Submenu/>   
        </AppProvider>             
        </>
    )
}

export default App5

