import React,{useEffect,useRef,useState} from 'react'
import {useGlobalContext} from './Context'

function Submenu() {
    const {isSubmenuOpen,location,pages}=useGlobalContext();
    const {page,links}=pages
    const [col,setCol]=useState('col-2');
    const container=useRef(null)
    useEffect(() => {
        setCol('col-2')
        const submenu=container.current;
        const {center,bottom}=location;
        submenu.style.left=`${center}px`;
        submenu.style.top=`${bottom}px`
        
        if(links.length===3) setCol('col-3')
        if(links.length>3) setCol('col-4')

    },[location,links])

    return (
        <aside className={`${isSubmenuOpen ? 'submenu show':'submenu'}`} ref={container}>
            <h3>{page}</h3>
            <div className={`submenu-center ${col}`}>
                {links.map((link,index)=>{
                    const {label,icon,url}=link
                    return (
                        <a key={index} href={url}>{icon}{label}</a>
                    )
                })}
            </div>
        </aside>
    )
}

export default Submenu
